// eslint-disable
import { useCallback, useEffect, useState } from "react";
import Modal from "react-modal";

import { BigNumber } from "ethers";
import { WeightedMultiOwner } from "./contract";
import { partition, prop } from "ramda";
import { useWeb3React } from "@web3-react/core";
import { Web3Provider } from "@ethersproject/providers";
import { useSnackbar } from "notistack";
import Style from "./Multisig.module.css";

type Transaction = {
  index: number;
  createdBy: string;
  destination: string;
  value: BigNumber;
  data: string;
  executed: boolean;
  voteWeight: BigNumber;
  confirmedBySender: boolean;
};

/**
 * @example
 * // returns [0, 1, 2, 3, 4]
 * range(5)
 */
const range = (toExcluse: number) => [...Array(toExcluse).keys()];

function AddTransactionForm({
  contract,
  updateTransactions,
}: {
  contract: WeightedMultiOwner;
  updateTransactions: () => Promise<void>;
}) {
  const [showForm, setShowForm] = useState(false);
  const [savingForm, setSavingForm] = useState(false);
  const [destination, setDestination] = useState("");
  const [value, setValue] = useState("");
  const [data, setData] = useState("");
  return (
    <>
      <div className="col text-center" style={{ paddingTop: "0px" }}>
        <button
          onClick={() => setShowForm(true)}
          className="btn btn-outline-secondary"
          style={{ width: "200px" }}
        >
          + Submit transaction
        </button>
      </div>
      <Modal
        isOpen={showForm}
        onRequestClose={() => setShowForm(false)}
        className="container-fluid txcont"
        style={{ content: { display: "flex", justifyContent: "center" } }}
      >
        <form
          onSubmit={e => {
            e.preventDefault();
            setSavingForm(true);
            contract
              .submitTransaction(destination.trim(), value.trim(), data.trim())
              .then(t => t.wait())
              .finally(() => {
                setSavingForm(false);
                setShowForm(false);
              })
              .then(updateTransactions);
          }}
        >
          <div className="row" style={{ textAlign: "center" }}>
            <label className="translabel"> Please fill in the following transaction data! </label>
          </div>
          <div className="row">
            <label className="translabel"> Destination </label>
            <input
              onChange={e => setDestination(e.target.value)}
              disabled={savingForm}
              pattern="\s*0[x][0-9a-fA-F]{40}\s*"
              placeholder="0xc02aaa39b223fe8d0a0e5c4f27ead9083c756cc2"
              required
              title="Ethereum address"
              type="text"
              value={destination}
            />
          </div>
          <div className="row">
            <label className="translabel">Value (Wei)</label>
            <input
              onChange={e => setValue(e.target.value)}
              disabled={savingForm}
              pattern="\s*\d+\s*"
              placeholder="0"
              required
              title="Non negative integer"
              type="string"
              value={value}
            />
          </div>
          <div className="row">
            <label className="translabel"> Data </label>
            <input
              className="transinp"
              onChange={e => setData(e.target.value)}
              disabled={savingForm}
              pattern="\s*0x([0-9a-fA-F][0-9a-fA-F])+\s*"
              placeholder="0x00"
              required
              title="Hexadecimal data with even number of digits"
              type="string"
              value={data}
            />
          </div>
          <div className="row" style={{ justifyContent: "center" }}>
            <input
              className="transbutton"
              type="submit"
              value={savingForm ? "Saving..." : "Save"}
              disabled={savingForm}
            />
          </div>
        </form>
      </Modal>
    </>
  );
}

function AddOwnerForm({
  contract,
  updateTransactions,
}: {
  contract: WeightedMultiOwner;
  updateTransactions: () => Promise<void>;
}) {
  const { enqueueSnackbar } = useSnackbar();
  const addError = (e: string) => enqueueSnackbar(e, { variant: "error" });
  const [showForm, setShowForm] = useState(false);
  const [savingForm, setSavingForm] = useState(false);
  const [address, setAddress] = useState("");
  const [weight, setWeight] = useState("");
  return (
    <>
      <div className="col text-center" style={{ paddingTop: "0px" }}>
        <button
          onClick={() => setShowForm(true)}
          className="btn btn-outline-secondary"
          style={{ width: "200px" }}
        >
          Add owner
        </button>
      </div>
      <Modal
        isOpen={showForm}
        onRequestClose={() => setShowForm(false)}
        className="container-fluid txcont"
        style={{ content: { display: "flex", justifyContent: "center" } }}
      >
        <form
          onSubmit={e => {
            e.preventDefault();
            try {
              const data = contract.interface.encodeFunctionData("addOwner", [
                address.trim(),
                weight,
              ]);
              setSavingForm(true);
              contract
                .submitTransaction(contract.address, 0, data)
                .then(t => t.wait())
                .finally(() => {
                  setSavingForm(false);
                  setShowForm(false);
                })
                .then(updateTransactions);
            } catch (e: any) {
              addError(e.message);
            }
          }}
        >
          <div className="row" style={{ textAlign: "center" }}>
            <label className="translabel"> Please fill in the following transaction data! </label>
          </div>
          <div className="row">
            <label className="translabel"> Address </label>
            <input
              onChange={e => setAddress(e.target.value)}
              disabled={savingForm}
              pattern="\s*0[x][0-9a-fA-F]{40}\s*"
              placeholder="0xc02aaa39b223fe8d0a0e5c4f27ead9083c756cc2"
              required
              title="Ethereum address"
              type="text"
              value={address}
            />
          </div>
          <div className="row">
            <label className="translabel">Weight</label>
            <input
              onChange={e => setWeight(e.target.value)}
              disabled={savingForm}
              placeholder="1"
              required
              title="Non negative integer"
              type="number"
              step="1"
              min="1"
              max="255"
              value={weight}
            />
          </div>
          <div className="row" style={{ justifyContent: "center" }}>
            <input
              className="transbutton"
              type="submit"
              value={savingForm ? "Saving..." : "Save"}
              disabled={savingForm}
            />
          </div>
        </form>
      </Modal>
    </>
  );
}

